.bg-image {
  z-index: -1000;
  position: fixed;
  top: 0;
  height: 100vh;
  width: 100vw;
  background-image: url('/public/assets/janna-bg.jpg');
  background-size: cover;
  background-repeat: no-repeat;
}

.bg-image::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(10px);
  pointer-events: none;
}

.layout {
  display: flex;
  justify-content: center;
  margin-top: 32px;
}

.calc-container {
  text-align: right;
  box-shadow: 4px 4px 8px 0px rgba(0, 0, 0, 0.185);
  background: #ffffffb6;
  border-radius: 4px;
  width: 50vw;
  margin-bottom: 32px;
}

.calc-container h2 {
  text-align: center;
}

@media only screen and (max-width: 900px) {
  .calc-container {
    width: 80%;
  }
  .calc-container h2 {
    font-size: 24px;
  }
}

.result {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 16px;
}
.result table {
  background: #ffffffb6;
  padding: 16px;
  border: 1px solid #899499;
  border-radius: 6px;
}
.result td:first-child {
  padding-left: 24px;
}
.result small {
  display: block;
  width: 100%;
  text-align: center;
  margin-top: 4px;
}

.footer {
  margin: auto;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(255, 255, 255, 0.65), rgba(0, 0, 0, 0));
  padding: 2px 6px;
  border-radius: 6px;
  margin-top: 32px;
  text-align: center;
  color: #000;
}
.footer p {
  font-size: 14px;
  margin: 0;
}
.footer a {
  color: rgb(11, 39, 98);
  text-decoration: none;
}
.footer a:hover {
  color: rgb(25, 78, 192);
}

.about-text {
  margin: 8px 0px;
}

tr:not(:last-child) td {
  padding-bottom: 4px;
  border-bottom: 1px solid rgb(212, 212, 212);
}

.icons {
  display: flex;
  justify-content: center;
}
.icons a {
  font-size: 24px;
  margin: 4px;
  color: #292929;
  transition: all 200ms ease-in-out;
}
a.facebook:hover {
  color: #1778F2;
}
a.youtube:hover {
  color: #FF0000;
}
a.github:hover {
  color: #095c4a;
}
a.linkedin:hover {
  color: #2867B2;
}
