@import url('./form.css');

*,
*:before,
*:after {
  box-sizing: border-box;
  font-family: 'Cairo', sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

hr {
  border: 0;
  height: 1px;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
}

.text-center {
  text-align: center;
}

.m-0 {
  margin: 0;
}

.mr-1 {
  margin-right: 8px;
}

.w-100 {
  width: 100%;
}

.w-30 {
  width: 30%;
}

.flex {
  display: flex;
  flex-wrap: wrap;
}

.inline {
  display: inline;
  margin-left: 4px;
}

.bold {
  font-weight: 700 !important;
}

.help {
  font-size: 12px;
  text-align: right !important;
}